import axios from 'axios';
const Index = (data) => {
	const rand = data[Math.random() * data.length()];
};

Index.getInitialProps = async () => {
	const res = await axios.get('https://quotesondesign.com/wp-json/wp/v2/posts/?orderby=rand');
	const { data } = res;
	console.log(data);
	return data;
};

export default Index;
